
const Initial_State = {
    enrollment: [],
    dayEnrollment: [],
    timeEnrollment: [],
    rotationEnrollment: [],
    classEnrollment: []

};


const Enrollment = (state = Initial_State, action) => {

    switch (action.type) {
        case 'SET_Enrollment': return {
            ...state,
            enrollment: [...state.enrollment, action.enrollment],
            dayEnrollment: [...state.dayEnrollment, action.dayEnrollment],
            timeEnrollment: [...state.timeEnrollment, action.timeEnrollment],
            rotationEnrollment: [...state.rotationEnrollment, action.rotationEnrollment],
            classEnrollment:[...state.classEnrollment, action.classEnrollment]
        }
        case 'SET_Enrollment_Temp': return {

            enrollment: action.enrollment,
            dayEnrollment: action.dayEnrollment,
            timeEnrollment: action.timeEnrollment,
            rotationEnrollment: action.rotationEnrollment,
            classEnrollment:action.classEnrollment,
        }
        case 'SECTOR_CLEAR': return Initial_State;

        default: return state;
    }



};


export default Enrollment;